// src/router/index.ts
import Vue from "vue";
import UploadFile from "@/components/UploadFile.vue";
import VueRouter from "vue-router";
import MainView from "@/components/MainView.vue";

Vue.use(VueRouter);

export default new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "Home",
      component: MainView,
    },
    {
      path: "/upload",
      name: "Upload",
      component: UploadFile,
    },
  ],
});
